import React from 'react';
import { Link } from 'react-router-dom';
// margin m, padding p, top t, bottom b, left s, right e, 위아래 y, 좌우 x 
// 글자크기 fs-1, 흰색 text-white 회색 text-muted, 파랑 text-primary 초록 text-success 하늘색 text-info 노랑색 text-warning 빨간색 text-danger
// font-weight fw-bold
// 텍스트 줄간격 lh-lg
import conceptjson from '../pages/analysis/analysis.json'
import catalystjson from '../pages/catalyst/catalyst.json'
import energyjson from '../pages/energy/energy.json'
import cvjson from '../pages/computervisuali/cv.json'
import objDetecterjson from '../pages/objDetecter/objDetecter.json'
import autoASMjson from '../pages/autoASM/autoASM.json'
import papersjson from '../pages/papers/papers.json'
import theoryjson from '../pages/theory/theory.json'

function Header() {
	const style = {
		fontSize: "25px",
		fontWeight: "bold",
		color: "#ddffaa"
  }
  return (
    <>
       <nav className="navbar navbar-expand-md fixed-top navbar-dark bg-primary p-2 ps-4" >
			 	<i className='fab fa-accusoft mx-2 fs-2 text-info' />
				<a class="navbar-brand" href="#" style={style}>Envi.</a>
				<button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
					<span class="navbar-toggler-icon"></span>
				</button>
				<div class="collapse navbar-collapse" id="navbarNav">
					<ul className="navbar-nav me-auto mb-2 mb-lg-0 fs-5" >
						<li><Link className="nav-link" to={"/"} >Home</Link></li>
						
						{/* https://www.w3schools.com/icons/fontawesome5_icons_computers.asp */}
						<li><i className='fas fa-flask mx-2 mt-2 fs-2 text-light' /></li>

						{/* Catalyst */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Catalyst
							</a>
							<ul className="dropdown-menu">
								{catalystjson.catalystData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Energy */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Energy
							</a>
							<ul className="dropdown-menu">
								{energyjson.energyData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Related Theory */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Theory
							</a>
							<ul className="dropdown-menu">
								{theoryjson.theoryData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Analysis */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Analysis
							</a>
							<ul className="dropdown-menu">
								{conceptjson.analysisData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* TiO2MoS2 Paper */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Papers
							</a>
							<ul className="dropdown-menu">
								{papersjson.papersData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* icon */}
						{/* https://www.w3schools.com/icons/fontawesome5_icons_computers.asp */}
						<li><i className='fas fa-keyboard mx-2 mt-2 fs-2 text-light' /></li>

						{/* CV */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								CV
							</a>
							<ul className="dropdown-menu">
								{cvjson.cvData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* Object Detecter */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
							Object Detecter
							</a>
							<ul className="dropdown-menu">
								{objDetecterjson.objDetecterData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* ASM System */}
						<li class="nav-item dropdown">
							<a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								ASM System
							</a>
							<ul className="dropdown-menu">
								{autoASMjson.autoASMData.map((items) => (
									<li><Link className="dropdown-item" to={items.url}>{items.title}</Link></li>
								))}
							</ul>
						</li>

						{/* test */}
						{/* <li><Link to={"/test"} className="nav-link">Test</Link></li> */}
						<li><Link to={"/git"} className="nav-link">git</Link></li>
					</ul>
				</div>


			</nav>
    </>
  );
}
export default Header;
