import React from 'react';
import {Switch, Router, Route} from 'react-router-dom';

import Home from '../pages/Home';

// theory
import BandTheory from '../pages/theory/BandTheory';
import ElectronicBandTheory from '../pages/theory/ElectronicBandTheory';
import 제일원리 from '../pages/theory/제일원리';
import 상태밀도함수 from '../pages/theory/상태밀도함수';
import D_Band_theory from '../pages/theory/d_Band_theory';
import 양자점 from '../pages/theory/양자점';
import Z_scheme from '../pages/theory/Z_scheme';
import H_junction from '../pages/theory/H_junction';

// analysis
import 표면분석 from '../pages/analysis/표면분석';
import XRD from '../pages/analysis/1XRD';
import Morphology from '../pages/analysis/2Morphology';
import XPS from '../pages/analysis/3XPS';
import Raman from '../pages/analysis/Raman';
import Optical from '../pages/analysis/4Optical';
import EDOS from '../pages/analysis/EDOS';
import PDOS from '../pages/analysis/PDOS';
import Surface from '../pages/analysis/5Surface';
import 서울대 from '../pages/theory/서울대';
import 계산화학 from '../pages/analysis/계산화학'

// catalyst
import Catalyst1 from '../pages/catalyst/catalyst1';
import Photoreactor from '../pages/catalyst/potoreactor';
import Provskites from '../pages/catalyst/perovskites';
import Quantumdots from '../pages/catalyst/quantumdots';
import Anodizing from '../pages/catalyst/anodizing';
import Black_tio2 from '../pages/catalyst/black_tio2';
import TMDCEpitaxy from '../pages/catalyst/TMDC-Epitaxy';
import Tio2mos2 from '../pages/catalyst/tio2mos2';
import N_reduction from '../pages/catalyst/N_reduction';
import Catalyst_old from '../pages/catalyst/catalyst_old';
import Catalyst_old2 from '../pages/catalyst/catalyst2_old';
import Lorentz_force from '../pages/catalyst/lorentz_force';
import Photothermal from '../pages/catalyst/Photothermal';
import up_conversion from '../pages/catalyst/up_conversion';
import Photon_avalanche from '../pages/catalyst/Photon_avalanche';
import plasmon from '../pages/catalyst/plasmon';
import two_dimensional from '../pages/catalyst/two_dimensional';

// energy
import Energy from '../pages/energy/energy';
import Co2 from '../pages/energy/co2';

// computer visualization
import Page1cv from '../pages/computervisuali/page1cv';
import Page2cv from '../pages/computervisuali/page2cv';

// objDetecter
import objDetect1 from '../pages/objDetecter/objDetect1';
import objDetect2 from '../pages/objDetecter/objDetect2';

// autoASM
import autoASM1 from '../pages/autoASM/autoASM1'
import autoASM2 from '../pages/autoASM/autoASM2';

// papers
import Paper from '../pages/papers/paper';
import MyPapers from '../pages/papers/my_papers';
import TiO2MoS2 from '../pages/papers/TiO2MoS2';

// test
import Test from '../pages/test';
import Git from '../pages/git';

// Route path='/page1' === Nav.Link href="/hawon/" 
//   실제 로드되는 페이지        어드레스에 표시

function NavRoute() {
  return (
    <>
      <Switch>
        <Route path={'/'} exact component={Home} />

        {/* // catalyst */}
        <Route path={'/catalyst/catalyst1'} component={Catalyst1} />
        <Route path={'/catalyst/photoreactor'} component={Photoreactor} />
        <Route path={'/catalyst/provskites'} component={Provskites} />
        <Route path={'/catalyst/anodizing'} component={Anodizing} />
        <Route path={'/catalyst/n_reduction'} component={N_reduction} />
        <Route path={'/catalyst/catalyst_old'} component={Catalyst_old} />
        <Route path={'/catalyst/Catalyst_old2'} component={Catalyst_old2} />
        <Route path={'/catalyst/lorentz_force'} component={Lorentz_force} />
        <Route path={'/catalyst/quantumdots'} component={Quantumdots} />
        <Route path={'/catalyst/black_tio2'} component={Black_tio2} />
        <Route path={'/catalyst/tmdcepitaxy'} component={TMDCEpitaxy} />
        <Route path={'/catalyst/tio2mos2'} component={Tio2mos2} />
        <Route path={'/catalyst/Photothermal'} component={Photothermal} />
        <Route path={'/catalyst/up_conversion'} component={up_conversion} />
        <Route path={'/catalyst/Photon_avalanche'} component={Photon_avalanche} />
        <Route path={'/catalyst/plasmon'} component={plasmon} />
        <Route path={'/catalyst/two_dimensional'} component={two_dimensional} />

        {/* // energy */}
        <Route path={'/energy/Energy'} exact component={Energy} />
        <Route path={'/energy/Co2'} component={Co2} />

        {/* // theory */}
        <Route path={'/theory/BandTheory'} component={BandTheory} />
        <Route path={'/theory/ElectronicBandTheory'} component={ElectronicBandTheory} />
        <Route path={'/theory/제일원리'} exact component={제일원리} />
        <Route path={'/theory/상태밀도함수'} exact component={상태밀도함수} />
        <Route path={'/theory/d_Band_theory'} component={D_Band_theory} />
        <Route path={'/theory/양자점'} exact component={양자점} />
        <Route path={'/theory/Z_scheme'} exact component={Z_scheme} />
        <Route path={'/theory/H_junction'} exact component={H_junction} />
        <Route path={'/theory/서울대'} component={서울대} />

        {/* // concept */}
        <Route path={'/analysis/표면분석'} exact component={표면분석} />
        <Route path={'/analysis/XRD'} component={XRD} />
        <Route path={'/analysis/Morphology'} component={Morphology} />
        <Route path={'/analysis/XPS'} component={XPS} />
        <Route path={'/analysis/Optical'} component={Optical} />
        <Route path={'/analysis/Raman'} component={Raman} />
        <Route path={'/analysis/EDOS'} exact component={EDOS} />
        <Route path={'/analysis/PDOS'} exact component={PDOS} />
        <Route path={'/analysis/Surface'} component={Surface} />
        <Route path={'/계산화학'} component={계산화학} />

        {/* // papers */}
        <Route path={'/papers/Paper'} exact component={Paper} />
        <Route path={'/papers/MyPapers'} component={MyPapers} />
        <Route path={'/papers/TiO2MoS2'} component={TiO2MoS2} />

        {/* -------------------------------------------------------- */}
        {/* // computer visualization */}
        <Route path={'/computervisuali/page1cv'} component={Page1cv} />
        <Route path={'/computervisuali/page2cv'} component={Page2cv} />

        {/* // objDetecter */}
        <Route path={'/objDetecter/objDetect1'} component={objDetect1} />
        <Route path={'/objDetecter/objDetect2'} component={objDetect2} />
        
        {/* // autoASM */}
        <Route path={'/autoASM/autoASM1'} component={autoASM1} />
        <Route path={'/autoASM/autoASM2'} component={autoASM2} />

        {/* // Test */}
        <Route path={'/test'} component={Test} />
        <Route path={'/git'} component={Git} />


      </Switch>
      
      {/* console.log('public url: ', {process.env.PUBLIC_URL}) */}
    </>

  )
};
export default NavRoute;