import React from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { dark } from 'react-syntax-highlighter/dist/cjs/styles/prism';

function objDetect1() {
  return (
    <>
    <div className="px-4 " style={{marginTop:80}}>
        <Contents />
    </div>
    </>
  );
}
export default objDetect1;

function Contents() {
	
	const title = 'objDetect1';

  return (
    <>
        <h1>{title}</h1>
				<div>

				<SyntaxHighlighter language="csharp" style={dark} showLineNumbers={true}>
					{`
						using System;
						using System.Collections.Generic;
						using System.ComponentModel;
						using System.Data;
						using System.Drawing;
						using System.Linq;
						using System.Text;
						using System.Threading.Tasks;
						using System.Windows.Forms;
						
						namespace csMetAnalysisF.Gaussian2D
						{
								public partial class prjSetting : Form
								{
										public prjSetting()
										{
												InitializeComponent();
										}
						
										private void btnOK_Click(object sender, EventArgs e)
										{
												g.address = txtAddress.Text;
										}
								}
						
						}
					
					`}
				</SyntaxHighlighter>

				</div>
    </>

  );
}	