import React from 'react';

function ElectronicBandTheory() {
	return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
	);
};

export default ElectronicBandTheory;

function Contents() {

	const title = 'Electronic Band Theory';

  return (
		<>
			<h1>{title}</h1>

		</>
	);
}	

