import React from 'react';

function H_junction() {
  return (
    <>
    <div className="px-4 " style={{marginTop:80}}>
        <Contents />
    </div>
    </>
  );
}
export default H_junction;

function Contents() {
	
	const title = 'H_junction';

  return (
    <>
        <h1>{title}</h1>

    </>

  );
}	