import React from 'react';

function 표면분석() {
  return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
  );
}
export default 표면분석;

function Contents() {
	
	const title = '표면 분석 기법';

  return (
    <>
      <h1>{title}</h1>
      <h3>분광법 기술을 통한 물질의 표면 화학 특성 검사</h3>

      <img src="../images/concept/17.jpg" width={'800'}/>

      <img src="../images/concept/18.jpg" width={'800'}/>

      <h5><u><b>X-ray photoelectron spectroscopy(XPS)</b></u></h5>
      <ul>
        <li>X선 광전자 분광법(XPS)은 표면 화학 특성 탐색에 일반적으로 사용되는 기법이다. </li>
        <li>XPS에서 얻은 데이터는 물질의 바깥 쪽 몇 나노미터에 대한 정량화된 조성을 제공한다. </li>
        <li>해당 데이터는 존재하는 원소와 그 원소에 대한 화학적 상태를 모두 상세하게 보여준다.</li>
        <li>재료의 성능을 이해하고 개선하는 데 있어 매우 중요한다. </li>
        <li>XPS의 표면 민감도는 일상적으로 사용되는 다른 분석 기법으로는 불가능한 수준으로 표면 화학 특성을 분석한다는 것을 의미한다. </li>
        <li>XPS 분석은 각각의 에칭(etching) 사이클 후에 이온 빔을 사용하여 물질을 천천히 제거하는 
        뎁스 프로파일링과 같은 프로세스를 통해 물질로 확장될 수 있다.</li>
        <li>뎁스 프로파일링을 통해 높은 수준의 분해능으로 조성 프로파일을 측정할 수 있다. </li>
        <li>뎁스 프로파일을 사용하면 표면의 부식, 표면 산화 등으로 인해 구성이 표면에서 벌크로 어떻게 변하는지 확인하거나, 
        서로 다른 물질이 서로 결합된 접면에서의 화학 작용을 이해할 수 있다.</li>
        <li>XPS는 표면 화학을 이해하기 위한 중요한 수단으로 다양한 산업 및 응용 분야에서 발생하는 문제점을 극복하기 위해 사용된다. </li>
        <li>물질 특성을 파악하는 데 한 가지 기법만을 사용하는 것은 충분하지 않다.</li>
        <li>완전한 이해를 위해서는 다른 분석이 필요할 수 있다. </li>
      </ul>

      <h5><u><b>Raman spectroscopy</b></u></h5>
      <ul>
        <li>Raman 분광법은 물질의 분자 결합을 이해하는데 사용되며 구조적 변화에 매우 민감한 기법이다. </li>
        <li>이 기술은 산란 기술 중 하나이지만 이 기법은 레이저 소스의 광자가 사용되며, 일반적으로 적외선에서 UV 파장까지 사용된다. </li>
        <li>입사 광자 중 일부는 Raman 산란이 이루어지면서 시료의 흥미로운 진동 모드를 통해 에너지를 상실한다. </li>
        <li>스펙트럼을 만들기 위해 이 산란된 광자가 탐지된다. </li>
        <li>Raman 분광법은 일반적으로 여기에 제시된 다른 기술에 비해 훨씬 더 넓은 분석 심도를 제공한다. </li>
        <li>벌크 정보가 표면 정보를 보완하는 역할을 하는 고분자와 깊이 척도가 우수하게 연관되는 
        그래핀 및 탄소 나노튜브와 같은 나노물질의 특성을 파악하는 데 유용하다.</li>
      </ul>

      <h5><u><b>Ion scatterring spectroscopy(ISS)</b></u></h5>
      <ul>
        <li>이온 산란 분광법(ISS) 또는 저에너지 이온 산란(LEIS)은 표면의 첫 번째 원자층의 원소 구성을 조사하는 데 사용되는 표면 감도가 높은 기법이다. </li>
        <li>프로브(probe)는 표면에서 산란되는 비활성 기체 이온의 빔을 사용한다.</li>
        <li>산란된 이온의 운동 에너지를 측정한다. </li>
        <li>입사 선속의 에너지, 이온 질량, 산란 각도 및 산란된 이온의 에너지가 파악되므로 표면 원자의 질량을 계산하는 데 운동량의 보존이 사용될 수 있다.</li>
        <li>이러한 상호작용은 가장 바깥쪽 표면층에서만 발생할 수 있으므로, ISS는 매우 효과적이다. </li>
        <li>이 기능은 표면 분리 및 층 발달을 조사하여 XPS의 구성 정보를 보완하는 데 사용된다.</li>
      </ul>

      <h5><u><b>Reflected electron energy loss spectroscopy(REELS)</b></u></h5>
      <ul>
        <li>반사된 전자 에너지 손실 분광법(REELS)은 표면에서 물질의 전자 구조 탐침에 사용되는 기법이다. </li>
        <li>ISS와 유사한 방식으로 작동하지만, 이 분광법의 입사 입자는 전자, 즉 산란된 전자 빔이다. </li>
        <li>입사 전자는 시료에서 전자 전이를 발생시킴으로써 에너지를 잃게 될 수 있으며, 
        이러한 에너지 손실은 REELS 실험으로 측정된다. 전자 밴드 갭 또는 비점유 분자 오비탈의 상대적 에너지 수준과 같은 속성을 측정할 수 있다. </li>
        <li>경우에 따라서는 XPS에서는 불가능한 수소를 감지할 수도 있다.</li>
      </ul>

      <h5><u><b>UV photoelectron spectroscopy(UPS)</b></u></h5>
      <ul>
        <li>UV 광전자 분광법(UPS)은 XPS와 매우 유사하지만, X선 광자가 아닌 UV 광자를 사용하여 표면에서 광전자를 여기시키는 기술이다. </li>
        <li>UV 광자는 운동 에너지가 낮기 때문에, 검출되는 광전자는 결합과 관련된 낮은 결합 에너지 수준에서 비롯된다. </li>
        <li>이것은 상당히 복잡한 영역이며 중복되는 피크가 매우 많지만, 화합물에 대한 흔적으로 기능할 수 있다. </li>
        <li>일반적으로 이 영역에서 수집된 데이터를 XPS 및 UPS와 비교하는 것이 도움이 된다.</li>
        <li>UPS 데이터는 전자 특성을 이해하기 위한 REELS의 데이터를 보완해 주며, 
        최대 에너지 사용 결합 상태에 대한 정보를 제공한다. </li>
        <li>광전자 스펙트럼의 폭은 적절한 시료의 작업 기능을 측정하는 데 사용할 수 있으며, 
        이는 비교적 짧은 전체 범위 규모(Al Ka X-선의 경우에는 0~1487eV 대신 0~22eV 또는 40eV)를 사용하여 쉽게 수행할 수 있다.</li>
      </ul>

      <h5><u><b>Auger electron spectroscopy(AES)</b></u></h5>
      <ul>
        <li>Auger 전자 분광학(AES)은 집속 전자 빔을 사용하여 표면 조성을 측정하는 기법이다. </li>
        <li>Auger 방출 프로세스는 전자가 방출된 후 원자를 이완시킴으로써 이루어집니다. </li>
        <li>쉘의 빈 공간이 다른 오비탈에서 온 전자로 채워지고, 이 과정에서 방출되는 추가 에너지가 또 하나의 전자 방출을 유발한다. </li>
        <li>Auger 특성은 XPS 스펙트럼에서 관찰되며, 이 프로세스는 광전자가 방출된 후에도 발생할 수 있다. </li>
        <li>그러나 AES의 경우, 일반적으로 전자 건을 사용하여 시료를 자극한다.</li>
        <li>오거 전자 분광법은 원소 및 일부 화학 상태 정보를 제공하여 XPS를 보완하며, 
        더 높은 공간 분해능이라는 장점을 제공한다.</li>
      </ul>


      <img src="../images/concept/19.jpg" width={'1000'}/>

    </>
  );
}	