import React from 'react';

function Photothermal() {
	return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
	);
};

export default Photothermal;


function Contents() {
	
	const title = 'Photothermal-enhanced catalyst';

  return (
		// 여기에 내용을 넣는다
		<div>
			<h1>{title}</h1>
			<div>
				<ul>
				<h5>------ 광열촉매 Photothermal-enhanced catalyst ------------------------ </h5>
					<ol>
						<li><a target='_blank'  href="https://pubs.rsc.org/en/content/articlelanding/2016/SC/C6SC03239G#!divAbstract">Cu7S4 microsphere@zeolitic</a></li>
						<li><a target='_blank'  href="https://www.sciencedirect.com/science/article/pii/S246802571730064X">A review on photo-thermal catalytic conversion of carbon dioxide</a></li>
						<li><a target='_blank'  href="https://chemistry-europe.onlinelibrary.wiley.com/doi/10.1002/cssc.201900175">환경에서의 열광 촉매UV / Vis / IR 분해</a></li>
						<li><a target='_blank'  href="https://pubs.rsc.org/en/content/articlelanding/2018/TA/C8TA01362D#!divAbstract">Co3O4 메조포러스 나노로드 공기중 벤젠 완전 분해(실험실 공기정화)</a></li>
						<li><a target='_blank'  href="https://pubs.rsc.org/en/content/articlelanding/2016/ta/c5ta07617j#!divAbstract">광/열촉매 활성이 뛰어난 초박형 메조포러스 Co3O4 나노시트</a></li>
						<li><a target='_blank'  href="https://www.sciencedirect.com/science/article/pii/S2211285519304860">Fe3Si assisted Co3O4</a></li>
						<li><a target='_blank'  href="https://www.sciencedirect.com/science/article/pii/S0169433219310852">NiCo2O4 - CuCo2O4 - FeCo2O4 - MnCo2O4</a></li>
						<li><a target='_blank'  href="https://pubs.rsc.org/en/content/articlelanding/2020/sm/d0sm00306a#!divAbstract">광열 변환 효율이 높은 폴리피롤 및 폴리아닐린 나노 복합물</a></li>
						<li><a target='_blank'  href="https://pubs.acs.org/doi/10.1021/acs.nanolett.9b01417">원자 적으로 얇은 자기 반도체 CrPS 4 의 광화학 및 광열 산화 사이의 교차</a></li>
					</ol>
				</ul>

			</div>

		</div>
	);
}	