import React from 'react';

function Z_scheme() {
  return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
  );
}
export default Z_scheme;

function Contents() {
	
	const title = 'Z_scheme';

  return (
    <>
      <h1>{title}</h1>
    </>
  );
}	