import React from 'react';

function D_Band_theory() {
	return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
	);
};

export default D_Band_theory;


function Contents() {
	
	const title = 'D_Band_theory';

  return (
		// 여기에 내용을 넣는다
		<>
			<h1>{title}</h1>
			<div>
				<h5>표면 화학</h5>
				
				<a target='_blank'  href="https://www.cheric.org/PDF/NICE/NI27/NI27-5-0557.pdf">연료전지 성능을 지배하는 촉매의 전자구조</a>
				<ul>
						<li>촉매표면과 유기물 분자사이의 화학적 결합은 촉매표면의 반응성을 이해하는 기본 기초입니다. </li>
						<li>d 밴드 모델은 Bjørk Hammer와 Jens Nørskov가 거의 30년 전에 전이 금속 표면과 흡착질의 상호 작용 경향을 설명하기 위해 제안했다. </li>
						<li>촉매 연구: 활성점 및 반응속도 해석</li>
						<li>촉매활성(활성점)의 본질은 흡착 특성을 결정하는 촉매의 전자구조(핵에 속박된 전자의 에너지상태)에 대한 연구</li>
						<li>전자구조 : 물리학적/양자화학적 개념(역격자, 운동량 공간, 전자 파동 함수)- 
							고분해능 전자분광(core level이 아닌 valence 부분), X선 흡수 실험(white line 해석) 등이 
							촉매연구를 위해 흔히 사용되는 이론 및 분석방법으로는 한계가 있다.</li>
						<li>따라서, 촉매의 전자구조 해석 및 활성과의 연관성에 대한 연구는 이론물리학 이용을 이용하게 되었다.</li>	
						<li>제일원리 계산결과를 바탕으로 금속의 d-band center model이 촉매의 전자구조 효과를 정리하였다</li>
						<li><b><u>전자 에너지 상태밀도(Density of State, DOS)</u></b>의 변화에 대한 해석에 d-band center model를 이용한다. </li>
						<li>컴퓨터의 발전으로 밀도범함수이론(Density Functional Theory, DFT)의 
							본격적인 활용에 의해 이론물리 분야에서의 전자구조 해석은 점점 더 그 정밀도를 높여갔다. </li>
						<li>DFT는 다전자 문제의 해결을 위해 오랫동안 사용되어 왔지만 
							복잡한 계산으로 악명 높았던 Hartree-Fork 방법의 전자 파동함수를 <b><u>전자 밀도함수</u></b>로 대체함으로써 
							종전에는 불가능했던 <b><u>벌크 재료의 해석</u></b>을 가능하게 한 획기적인 이론이다. (Prof.Kohn 1998 노벨화학상) </li>
				</ul>
				<img src="../images/concept/2.PNG" width={'auto'}/>
				<p>[그림 1]에서 회색부분은 금속 표면의 전자 중 d 오비탈에서 유래된 밴드 구조를 나타내고 있으며,
					파란색 부분은 금속 표면에 흡착된 산소의 2p 오비탈에서 유래된 밴드 구조를 표현하고 있다. 
					우선 주목해야 할 부분은 산소의 2p 밴드인데, -6eV(ε-εF)부근의 상대적으로 큰 피크는 금속 표면과 산소의
					안정한 결합을 형성하는 bonding state이며, 0 또는 -1eV 부근의 작은 피크는 결합을 깨는 반결합성(백
					금과 산소사이에 node가 형성되어 |Ψ|2=0)의 antibonding state이다. </p>

				<img src="../images/concept/3.PNG" width={'auto'}/>

				<p>Ru의 경우를 보면 페르미 레벨 밑으로 bonding state에만 전자가 채워져 있으므로 
					산소와 강한 결합을 형성할 것으로 예상되며, Cu, Ag, Au의 경우는 bonding state 뿐만 아니라 
					antibonding state까지 전자가 채워져 있으므로 흡착이 약할 것으로 예상된다. 
					그러나 Ni, Pd, Pt의 경우 antibonding state가 반만 채워져 있으므로 적절한 흡착세기를 가질 것으로 예측되는데 
					이것이 바로 10족 금속이 촉매로서 활용도가 가장 높은 이유이며, 
					또한 적절한 산소의 흡착세기를 가지고 프로톤두 개와 전자 두 개를 결합시켜 물을 형성하는 
					연료 전지 cathode 촉매로서 Pt 이외에 딱히 대안이 없는 상황도 이 때문이다. 
					이러한 경향을 자세히 관찰해 보면  짐을 알 수 있다. 
					따라서 d-band center position이 흡착세기를 결정하는 가장 중요한 파라미터인 것을 확인할 수 있다.</p>
				<p></p>

				<h5><b><u>제일원리 계산, d-band center position, 흡착세기(Binding Energy)</u></b></h5>
				<ul>
					<li>촉매의 d-band center가 페르미 레벨로부터 멀어질수록(ε-εF가 클수록) 흡착이 약해지며,</li>
					<li>가까울수록(ε-εF가 작을수록) 흡착이 강해진다.</li>
					<li>따라서, <b><u>d-band center position이 흡착세기(Binding Energy, BE)를 결정</u></b>하는 가장 중요한 파라미터 이다. </li>
					<li>촉매와 물질(산소, 수소 등)의 <b><u>activation barrier를 줄이기 위해 흡착세기</u></b>를 줄여야 한다.</li>
					<li>이를 위해서는 <b><u>d-band center를 페르미 레벨로부터 멀리하는 설계 전략이 필요하다.</u></b></li>
					<li>훕착을 약하게 하기 위해서는 <b><u>전자구조를 조절</u></b>해야 한다.</li>
					<li><b><u> 전자구조를 조절방법은  전이금속과의 합금을 형성하는 방법이며 2원계 합금부터 심지어 4원계 합금까지 수많은 연구 결과가 있다.</u></b></li>
				</ul>


			</div>

				<h3>표면 화학 및 촉매 작용의 밀도 범함수 이론</h3>
				<br/>
				<a target='_blank'  href="https://www.pnas.org/doi/10.1073/pnas.1006652108">표면 화학 및 촉매 작용의 밀도 범함수 이론</a>
					<ol>
						<li>표면 화학은 고체, 액체, 기체 사이의 경계에서 발생하며 흡착, 응집의 물리화학적 해석에 이용한다.</li>
						<li>Hammer와 Nørskov의 d-band center 모델은 전이 금속(TM) 표면의 촉매 활성을 이해하고 예측하는 데 널리 사용되고 있다.</li>
					</ol>

					<h4>표면 화학의 밀도 함수 이론 계산</h4>
					<ul>
						<li>d-밴드 모델은 전이 금속 표면에서의 결합 형성에 대한 대략적인 설명입니다 .</li>
						<li>흡착 원자가 상태와 전이 금속 표면의 s 및 d 상태 사이의 상호 작용을 설명</li>
					</ul>
					<img src="../images/concept/1.png" width="900" ></img>

				<ul>
					<li><a target='_blank'  href="https://www.nature.com/articles/srep35916">An improved d-band model of the catalytic activity of magnetic transition metal surfaces</a></li>
				
					<p></p>
					<img src="../images/concept/4.PNG" width="900" ></img>
					<p></p>
					<img src="../images/concept/5.PNG" width="900" ></img>
				</ul>

				<br/>
				<a target='_blank'  href="https://pubs.acs.org/doi/10.1021/ja072281h">Surface Structures of Rutile TiO2 (011)</a>
		</>
	);
}	