import React from 'react';

function EDOS() {
  return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
  );
}
export default EDOS;

function Contents() {
	
	const title = 'EDOS(electronic density of states)';

  return (
    <>
        <h1>{title}</h1>

    </>

  );
}	