import React from 'react';

function autoASM1() {
  return (
    <>
      <div className="px-4" style={{marginTop:80}}>
          <Contents />
      </div>
    </>
  );
}
export default autoASM1;

function Contents() {
	
	const title = 'autoASM1';

  return (
    <>
        <h1>{title}</h1>
				<img src='../images/autoASM/1.PNG' width="auto" height="auto" />
    </>

  );
}	